@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app-container {
  display: flex;
  flex-direction: column;
}
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}

nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 50px;
  position: fixed;
  width: 100%;
  padding: 1.5rem 0;
  -webkit-transition: background 0.5s ease-in-out, padding 0.5s ease-in-out;
  transition: background 0.5s ease-in-out, padding 0.5s ease-in-out;
  z-index: 9999;
}

.nav-collapse {
  background-color: #1d2731;
  padding: 0;
}

.logo {
  color: #e2e2e2;
  letter-spacing: 4px;
  font-size: 1.5rem;
  font-weight: 300;
}

.nav-links li {
  list-style: none;
  float: left;
}

.nav-links p {
  color: #e2e2e2;
  text-decoration: none;
  letter-spacing: 3px;
  font-size: 1rem;
  padding: 10px;
}

.logo:hover {
  color: #c1c1c1;
  cursor: pointer;
}

.nav-links p:hover {
  cursor: pointer;
  background-color: #334456;
}

.burger div {
  width: 25px;
  height: 3px;
  color: #e2e2e2;
  margin: 5px;
  cursor: pointer;
}

.burger {
  display: none;
}

.burger div {
  width: 25px;
  height: 3px;
  background-color: #e2e2e2;
  margin: 5px;
  cursor: pointer;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

@media only screen and (max-width: 768px) {
  nav {
    position: fixed;
    background-color: #1d2731;
  }

  .logo {
    font-size: 0.8rem;
  }

  .nav-links {
    position: absolute;
    right: 0px;
    min-height: 100vh;
    top: 3rem;
    padding-top: 1.5rem;
    background-color: #1d2731;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 50%;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    -webkit-transition: -webkit-transform 0.5s ease-in;
    transition: -webkit-transform 0.5s ease-in;
    transition: transform 0.5s ease-in;
    transition: transform 0.5s ease-in, -webkit-transform 0.5s ease-in;
  }

  .nav-links li {
    opacity: 0;
  }

  .nav-links p {
    letter-spacing: normal;
    font-size: 1rem;
    cursor: pointer;
  }

  .burger {
    display: block;
    cursor: pointer;
  }
}

.nav-active {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

@-webkit-keyframes navLinkFade {
  from {
    opacity: 0;
    -webkit-transform: translateX(50px);
            transform: translateX(50px);
  }

  to {
    opacity: 1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}

@keyframes navLinkFade {
  from {
    opacity: 0;
    -webkit-transform: translateX(50px);
            transform: translateX(50px);
  }

  to {
    opacity: 1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}

/* Mobile Menu Icon */
.toggle .line1 {
  -webkit-transform: rotate(-45deg) translate(-5px, 6px);
          transform: rotate(-45deg) translate(-5px, 6px);
}

.toggle .line2 {
  opacity: 0;
}

.toggle .line3 {
  -webkit-transform: rotate(45deg) translate(-5px, -6px);
          transform: rotate(45deg) translate(-5px, -6px);
}

div.home {
  min-height: 80vh;
  display: flex;
  background-color: rgba(0, 0, 0, 0.5);
}

.home-image-superlow-res,
.home-image-low-res,
.home-image-high-res {
  min-height: inherit;
  min-width: 100%;
  position: absolute;
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-blend-mode: multiply;
}

.home-image-superlow-res {
  z-index: -3;
  background-image: url(/portfolio-william/static/media/wallpaper-superlow-res.a963da40.png);
}

.home-image-low-res {
  z-index: -2;
  background-image: url(/portfolio-william/static/media/wallpaper-low-res.709215f3.png);
}

.home-image-high-res {
  z-index: -1;
  background-image: url(/portfolio-william/static/media/wallpaper-high-res.28a14341.jpg);
}

.home-container {
  height: auto;
  margin-left: auto;
  margin-right: auto;
  width: 60%;
  padding-top: 20rem;
  align-items: center;
  text-align: center;
  justify-content: center;
}

.home-heading-2 {
  font-size: 3.8rem;
  font-weight: 300;
  letter-spacing: 3px;
  color: #e2e2e2;
}

.home-description {
  padding: 1.5rem;
  color: #e2e2e2;
}

.expandarrow-icon {
  height: 3rem;
  width: 3rem;
  margin-top: 10rem;
  fill: #e2e2e2;
}

.expandarrow-icon:hover {
  cursor: pointer;
  -webkit-transform: translateY(-4px);
          transform: translateY(-4px);
}

@media only screen and (max-width: 768px) {
  .home-container {
    padding-top: 10rem;
    width: 80%;
  }

  .expandarrow-icon {
    margin-top: 5rem;
  }
}

div.about {
  min-height: 65vh;
  background-color: #ffffff;
  display: flex;
}

.about-container {
  height: auto;
  padding-top: 7rem;
  margin-left: auto;
  margin-right: auto;  
  width: 60%;
  align-items: center;
  justify-content: center;
}

.about-image {
  float: left;
  max-width: 50%;
  height: auto;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.5);
}

.about-text {
  float: right;
  width: 50%;
  height: auto;
  padding: 5px;
  padding: 0 1rem 0 1rem;
}

.about-heading-2 {
  font-size: 2rem;
  font-weight: 300;
  color: #1d2731;
}

.about-description {
  font-size: 1rem;
  color: #000;
  margin-top: 1.5rem;
  line-height: 1.5;
}

.about-link {
  color: #00009c;
  text-decoration: none;
  font-weight: 1000;
}

.about-link:link {
  color: #00009c;
}

.about-link:visited {
  color: #00009c;
}

@media only screen and (max-width: 1200px) {
  .about-container {
    width: 80%;
  }

  .about-heading-2 {
    font-size: 1.5rem;
  }

  .about-description {
    font-size: 0.8rem;
    width: 100%;
  }
}

@media only screen and (max-width: 768px) {
  .about-container {
    width: 100%;
    text-align: center;
    padding-top: 5rem;
  }

  .about-image {
    width: 100%;
    float: none;
    display: inline-block;
    margin-bottom: 3rem;
  }

  .about-text {
    width: 100%;
    margin-top: -1.5rem;
    padding: 0 0.5rem 0 0.5rem;
  }

  .about-description {
    font-size: 0.8rem;
  }
}

.skills-container {
  background-color: #f0f0f0;
  display: flex;
  flex-direction: column;
  padding: 3rem 0;
  min-width: 100%;
  max-width: 100%;
  background: #f0f0f0;
}

.skills-row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  height: inherit;
}

.skills-column {
  width: 30%;
}

@media only screen and (max-width: 870px) {
  .skills-column {
    width: 40%;
  }
}

@media only screen and (max-width: 660px) {
  .skills-column {
    width: 70%;
  }

  .skills-category-title {
    margin-top: 1.5rem;
  }
}

@media only screen and (max-width: 390px) {
  .skills-column {
    width: 90%;
  }
}

.skills-title {
  text-align: center;
  font-weight: 400;
  margin-top: 3rem;
  margin-bottom: 2rem;
  -webkit-user-select: none;
}
.skills-category-title {
  font-size: 1.5rem;
  text-align: center;
  font-weight: 600;
  margin-bottom: 1rem;
}
.skills-svg-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.skills-card {
  background: white;
  border-radius: 0.5rem;
  margin: 0.5rem;
}
.skills-card:hover {
  -webkit-transform: translateY(-0.2rem);
          transform: translateY(-0.2rem);
  box-shadow: 0 0.4rem 1rem rgba(0, 0, 0, 0.3);
}
.skills-svg {
  margin: 1rem;
  height: 5rem;
  width: 5rem;
}

.skills-svg-name {
  font-size: 1.1rem;
  text-align: center;
  margin-top: 0;
  padding: 0 0.2rem 0.2rem 0.2rem;
  -webkit-user-select: none;
}

.projects-container {
  min-height: 100vh;
  background-color: #314455;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  padding: 3rem;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.projects-heading-2 {
  font-size: 3rem;
  font-weight: 300;
  color: #e2e2e2;
  padding: 2rem;
}

.projects-row-container {
  width: auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 2rem;
  justify-content: center;
}

@media only screen and (max-width: 768px) {
  .projects-container {
    width: 100%;
  }

  .projects-row-container {
    margin: 1rem;
  }
}

.projectCard-container {
  background-color: #1d2731;
  margin: 2rem;
  padding: 0.5rem 0.5rem 1.2rem 0.5rem;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 30%;
  box-shadow: 0 2rem 3rem rgba(0, 0, 0, 0.5);
  -webkit-transition: -webkit-transform 0.2s;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
}

.projectCard-container:hover {
  -webkit-transform: translateY(-0.5rem);
          transform: translateY(-0.5rem);
}

.projectCard-container h2 {
  font-size: 1.8rem;
  font-weight: 300;
  color: #e2e2e2;
  padding: 1rem;
}

.projectCard-image {
  width: 90%;
  border-radius: 0.5rem;
}

.projectCard-content {
  margin: 1rem;
  display: flex;
}

.projectCard-content p {
  color: #e2e2e2;
  padding: 0.5rem 0 0 1.5rem;
}

.projectCard-description {
  color: #e2e2e2;
  text-align: center;
  padding: 0rem 1.5rem 0rem 1.2rem;
}

.projectCard-links {
  margin-top: 1rem;
  display: flex;
}

.projectCard-btn {
  background-color: #e2e2e2;
  border-radius: 0.2rem;
  text-decoration: none;
  display: flex;
  align-items: center;
  font-size: 1.2rem;
  padding: 0.5rem;
  margin-right: 1rem;
  margin-left: 1rem;
}

a:link,
a:visited,
a:hover,
a:active {
  color: #000;
}

.projectCard-btn:hover {
  cursor: pointer;
}

.projectCard-icon {
  width: 1.5rem;
  height: 1.5rem;
  margin-right: 0.5rem;
}

@media only screen and (max-width: 1200px) {
  .projectCard-container {
    width: 40%;
  }
}

@media only screen and (max-width: 1000px) {
  .projectCard-container {
    width: 60%;
    margin: 0.4rem;
    margin-bottom: 1rem;
  }
}

@media only screen and (max-width: 769px) {
  .projectCard-container {
    width: 70%;
    margin: 0.4rem;
    margin-bottom: 1rem;
  }
}

@media only screen and (max-width: 560px) {
  .projectCard-container {
    width: 100%;
    margin: 0.4rem;
    margin-bottom: 1rem;
  }
}

@media only screen and (max-width: 420px) {
  .projectCard-container {
    min-width: 130%;
  }

  .projectCard-content {
    display: block;
  }

  .projectCard-content p {
    padding: 0.2rem;
  }

  .projectCard-links {
    display: block;
  }

  .projectCard-btn {
    margin-bottom: 1rem;
  }
}

